/* Timeline.css */
.timeline {
    position: relative;
    width: 100%;
    padding: 20px 0;
  }
  
  .line {
    position: absolute;
    width: 4px;
    background-color: #ccc;
    top: 0;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
  }
  
  .timeline-event {
    position: relative;
    width: 50%;
    padding: 10px 20px;
    box-sizing: border-box;
    cursor: pointer;
  }
  
  .timeline-event.left {
    left: 0;
    text-align: right;
  }
  
  .timeline-event.right {
    left: 50%;
  }
  
  .dot {
    position: absolute;
    width: 12px;
    height: 12px;
    background-color: #ccc;
    border-radius: 50%;
    top: 50%;
    transform: translateY(-50%);
  }
  
  .timeline-event.left .dot {
    right: -6px;
  }
  
  .timeline-event.right .dot {
    left: -6px;
  }
  
  .event-details {
    display: inline-block;
    vertical-align: middle;
  }
  
  .period {
    display: block;
    font-weight: bold;
  }
  
  .event-name {
    display: block;
  }
  